.clock {
  position: relative;
  border-radius: 50%;
  background: url(../assets/clockface.svg) no-repeat center;
  height: 240px; }
  .clock:after {
    background: black;
    border-radius: 50%;
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 15px;
    height: 15px; }
  .clock .hand-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
  .clock .seconds-container {
    -webkit-animation: rotate 60s infinite steps(60);
            animation: rotate 60s infinite steps(60); }
  .clock .hour-hand {
    background: #65B2DA;
    position: absolute;
    border-radius: 85%/10%;
    width: 5px;
    height: 20%;
    left: 49%;
    top: 30%;
    -webkit-transform-origin: 50% 100%;
        -ms-transform-origin: 50% 100%;
            transform-origin: 50% 100%; }
  .clock .minute-hand {
    background: #5FBA81;
    position: absolute;
    border-radius: 150%/10%;
    width: 5px;
    height: 35%;
    left: 49%;
    top: 15%;
    -webkit-transform-origin: 50% 100%;
        -ms-transform-origin: 50% 100%;
            transform-origin: 50% 100%; }
  .clock .second-hand {
    background: #EA7264;
    position: absolute;
    border-radius: 200%/10%;
    width: 2px;
    height: 30%;
    left: 49.5%;
    top: 20%;
    -webkit-transform-origin: 50% 100%;
        -ms-transform-origin: 50% 100%;
            transform-origin: 50% 100%; }

@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

@keyframes rotate {
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }



